    min-height: 600px;
    font-size: 20px;
    font-family: sans-serif;
    flex-wrap: wrap;
}
#profile{
    flex-grow: 1;
    background-color: #f5f5f5d3;
    flex-direction: column;
    padding-top: 40px;
}
#profilephoto{
    display: block;
    margin: auto;
    padding: 10px;
    width: 250px;
    height: 250px;
}

